.l-header {
	width: 100%;
	height: 80px;
	display: flex;
	justify-content: space-between;
	.logo {
		width: 20%;
		display: flex;
		justify-content: center;
		align-items: center;
		@media screen and (max-width: 1080px) {
			width: 45%;
			a {
				width: 90%;
				img {
					width: 100%;
				}
			}
		}
		img {
			width: 100%;
		}
	}
	.header-menu {
		width: 520px;
		@media screen and (max-width: 1080px) {
			display: none;
		}
		nav {
			ul {
				display: flex;
				justify-content: space-between;
				li {
					height: 80px;
					a {
						line-height: 80px;
						font-size: 0.8125rem;
						text-decoration: none;
						color: #333;
					}
				}
			}
		}
	}
	.navbtn {
		display: none;
		@media screen and (max-width: 1080px) {	
			position: relative;
			z-index: 10;
			justify-content: center;
			display: flex;
			right: 0;
			width: 50px;
			height: 80px;
			box-sizing: border-box;
			border: none;
			cursor: pointer;
			i {
				display: block;
				width: 30px;
				height: 3px;
				background-color: #333;
				position: absolute;
				transition: transform 0.5s, opacity 0.5s;
				&:nth-child(1) {
					top: 25px;
				}
				&:nth-child(2){
					top: -10px;
					bottom: 0;
					margin: auto;
				}
				&:nth-child(3){
					bottom: 35px;
				}
			}
			.navbtn_txt_menu {
				position: absolute;
				color: #333;
				font-size: 10px;
				bottom: 5px;
			}
			.navbtn_txt_close {
				font-size: 10px;
				color: transparent;
				display: none;
			}
			&.active {
				position: fixed;
				.navbtn_txt_close {
					position: absolute;
					color: #0c2e5a;
					font-size: 10px;
					bottom: 5px;
					display: block;
				}
				.navbtn_txt_menu {
					display: none;
				}
				i {
					background-color: #0c2e5a;
					&:nth-child(1) {
						transform: translateY(9px) rotate(-45deg);
					}
					&:nth-child(2) {
						opacity: 0;
					}
					&:nth-child(3) {
						transform: translateY(-8px) rotate(45deg);
					}
				}
			}
		}
	}
	.header-contact {
		display: flex;
		@media screen and (max-width: 1080px) {
			display: none;
		}
		.tel {
			display: flex;
			align-items: center;
			text-align: center;
			margin-right: 20px;
			.inner {
				p {
					span {
						color: #01588E;
						font-weight: bold;
						font-size: 20px;
					}
					margin-bottom: 10px;
				}
				p.smaller {
					margin-bottom: 0 !important;
					font-size: 11px;
				}
			}
		}
		.apply-form {
			width: 170px;
			display: flex;
			align-items: center;
			justify-content: center;
			.inner {
				p.smaller {
					margin: 0 auto;
					width: 130px;
					font-size: 12px;
					text-align: center;
					padding-top: 2px;
					padding-bottom: 2px;
					color: #FFF;
					background-color: #000;
					border-radius: 9px;
				}
				.bigger {
					font-size: 20px;
				}
			}
			background-color: #FADD62;
		}
	}
}